import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";


Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Menu Principal",
    component: Home,
  },
  {
    path: "/carregar",
    name: "Carregar",
    component: () => import("@/views/carregamento"),
  },
  {
    path: "/servidorRemoto",
    name: "Servidor remoto",
    component: () => import("@/views/servidorRemoto"),
  },

];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
